$pure-black: black;
$pure-white: white;
$sandy-color: #f3dcc2;
.main {
    margin-top: 5%;
    padding: 10px;
    .container {
        margin-top: 10px;
        padding: 10px;
        .description {
            display: flex;
            justify-content: center;
            align-items: center;
            column-gap: 10px;
            .description-text {
                display: flex;
                flex-direction: column;
                text-align: left;
                row-gap: 10px;
                h1 {
                    padding: 10px;
                    width: 700px;
                    font-size: 48px;
                }
                .description-btn {
                    margin-left: auto;
                    padding-top: 2px;
                    text-align: center;
                    width: 200px;
                    height: 50px;
                    font-size: 32px;
                    border: .5px solid $pure-black;
                    border-radius: 20px;
                    color: $pure-black;
                    background-color: rgba($color: $sandy-color, $alpha: .4);
                }

                .description-btn:hover {
                    opacity: .8;
                }

                .description-btn:active {
                    transform: scale(.9);
                }


            }
            .descriptionImg {
                margin: 20px;
                width: 500px;
                height: 350px;
                border-radius: 30px;
                box-shadow: 5px 5px 10px 10px;
                color: grey;
            }
        } 
    }
}

@media (max-width:1200px) {
    .main {
        margin-top: 5%;
        .container {
            max-width: 1000px;

            .description {

                .description-text {
                    h1 {
                        width: 400px;
                        font-size: 36px;
                    }

                    .description-btn {

                    }
                }
                .descriptionImg {
                    width: 400px;
                    height: 250px;
                }
            }
        }
    }
}

@media (max-width:800px) {
    .main {
        .container {
            margin-top: 5%;
            padding: 10px;
            max-width: 600px;
            .description {
                .description-text {
                    
                    h1 {
                        padding: 10px;
                        width: 300px;
                        font-size: 28px;
                    }
                    .description-btn {
                        padding: 2px;
                        width: 100px;
                        height: 30px;
                        font-size: 20px;
                    }

                }
                .descriptionImg {
                    width: 350px;
                    height: 200px;
                }
            } 
        }
    }
}

@media (max-width:600px) {
    .main {
        .container {
            margin-top: 10%;
            padding: 10px;
            max-width: 500px;
            .description {
                column-gap: 20px;

                .description-text {
                    
                    h1 {
                        padding: 10px;
                        width: 200px;
                        font-size: 20px;
                    }
                    .description-btn {
                        padding: 3px 2px;
                        width: 80px;
                        height: 30px;
                        font-size: 16px;
                    }

                }
                .descriptionImg {
                    width: 250px;
                    height: 200px;
                }
            } 
        }
    }
}

@media (max-width:430px) {
    .main {
        .container {
            max-width: 300px;
            .description {
                flex-direction: column;
                column-gap: 10px;
                
                .description-text {
                    align-items: center;
                    h1 {
                        padding: 10px;
                        width: 300px;
                        font-size: 22px;
                    }
                    .description-btn {
                        margin-left: 0;
                        padding: 3px 2px;
                        width: 80px;
                        height: 30px;
                        font-size: 16px;
                    }

                }
                .descriptionImg {
                    width: 280px;
                    height: 200px;
                }
            } 
        }
    }
}