.nav {
    padding: 10px;
    background-color: #f3dcc2;
    border-bottom: 1px solid black;
    transition: .5 ease-in;
    .nav-wrapper {
        display: flex;
        justify-content: flex-end;
        align-items: center;
        margin-right: 10%;
        padding: 10px;
        
        .logo {
            margin-right: auto;
            font-size: 64px;
            font-weight: 700;

            a {
                color: black;
            }
        }

        .nav-list {
            display: flex;
            flex-wrap: wrap;
            column-gap: 30px;
            padding: 5px;

            .nav-list__item {
                position: relative;
                font-size: 24px;
                a {
                    color: black;
                }
            }

            .nav-list__item:hover {
                opacity: .6;
                text-decoration: underline white;
            }

            .basketCount,
            .favoritesCount {
                position: absolute;
                top: 60%;
                left: 100%;
                width: 25px;
                height: 25px;
                border-radius: 50%;
                background-color: rgba($color: white, $alpha: .4);
                font-size: 22px;
            }
            
        }
    }

}

@media (max-width:1200px) {
    .nav {
        padding: 5px;
            .container {
                padding: 0;
                max-width: 1000px;
            }
            .nav-wrapper {

            .logo {
                font-size: 54px;
            }
            }  
    }
}

@media (max-width:800px) {
    .nav {
        .container {
            max-width: 600px;
            padding: 0;
            .nav-wrapper {
                .logo {
                    font-size: 32px;
                }
        
                .nav-list {
                    column-gap: 20px;        
                    .nav-list__item {
                        font-size: 18px;
                    }
        
                    .basketCount,
                    .favoritesCount {
                        font-size: 18px;
                    }
                    
                }
            }
        }
    }
}

@media (max-width:600px) {
    .nav {
        .container {
            max-width: 500px;
            padding: 0;
            .nav-wrapper {
                .logo {
                    font-size: 28px;
                }
        
                .nav-list {
                    .nav-list__item {
                        font-size: 18px;
                    }

                    .basketCount,
                    .favoritesCount {
                        position: absolute;
                        top: 60%;
                        left: 100%;
                        width: 20px;
                        height: 20px;
                        border-radius: 50%;
                        background-color: rgba($color: white, $alpha: .4);
                        font-size: 18px;
                    }
                    
                }
            }
        }
    }
}

@media (max-width:430px) {
    .nav {
        .container {
            max-width: 300px;
            padding: 0;
            .nav-wrapper {
                .logo {
                    font-size: 24px;
                }
        
                .nav-list {
                    flex-direction: column;
                    align-items: start;
                    row-gap: 3px;
                    .nav-list__item {
                        font-size: 16px;
                    }

                    .basketCount,
                    .favoritesCount {
                        padding-top: 1px;
                        width: 15px;
                        height: 15px;
                        top: 0%;
                        left: 120%;
                        font-size: 12px;
                    }
                    
                }
            }
        }
    }
}