.books {
  padding: 10px;
}
.books .container {
  padding: 5px;
}
.books .container .search {
  display: flex;
  justify-content: center;
  -moz-column-gap: 10px;
       column-gap: 10px;
  margin-bottom: 40px;
  padding-left: 10px;
  align-content: center;
}
.books .container .search .search-open {
  padding: 5px;
  width: 400px;
  height: 30px;
  border: 0.5px solid black;
  border-radius: 20px;
  animation: open 2s linear;
}
.books .container .search .search-closed {
  padding: 0px;
  width: 0px;
  height: 0px;
  border: 0px solid black;
  border-radius: 20px;
  animation: close 2s linear;
}
.books .container .search .search-img {
  width: 30px;
  height: 30px;
}
.books .container .search .search-img:hover {
  opacity: 0.8;
}
.books .container .books-list {
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
  position: relative;
  padding: 10px;
  gap: 20px;
}
.books .container .books-list .books-list__item {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
  row-gap: 5px;
  padding: 5px;
  width: 350px;
  height: 350px;
  border: 0.5px solid #f3dcc2;
  border-radius: 20px;
  background-color: rgba(243, 220, 194, 0.2);
  transition: linear 2s;
}
.books .container .books-list .books-list__item .books-list__img-wrapper {
  width: 90%;
  height: 90%;
  border-radius: 20px;
}
.books .container .books-list .books-list__item .books-list__img-wrapper .books-item__img {
  margin-top: 5px;
  width: 200px;
  height: 250px;
  border-radius: 15px;
  box-shadow: 2px 2px 4px 4px;
  color: grey;
}
.books .container .books-list .books-list__item .books-list__img-wrapper .books-item__img:hover {
  transform: scale(1.2);
}
.books .container .books-list .books-list__item .books-item__title {
  width: 70%;
  color: black;
  font-size: 14px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
}
.books .container .books-list .books-list__item .books-item__price {
  align-items: start;
  width: 70%;
  color: black;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
}
.books .container .books-list .books-list__item .ageRestrictions {
  display: block;
  position: absolute;
  top: 90%;
  left: 90%;
  padding-top: 5px;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background-color: white;
  border: 1px solid black;
}
.books .container .books-list .books-list__item .bookPage-btn__wrapper {
  display: flex;
}
.books .container .books-list .books-list__item .bookPage-btn__wrapper .delete,
.books .container .books-list .books-list__item .bookPage-btn__wrapper .add {
  width: 40px;
  height: 30px;
  border: 0.5px solid white;
  border-radius: 40px;
  font-size: 22px;
  color: white;
}
.books .container .books-list .books-list__item .bookPage-btn__wrapper .add:hover,
.books .container .books-list .books-list__item .bookPage-btn__wrapper .delete:hover {
  opacity: 0.8;
}
.books .container .books-list .books-list__item .bookPage-btn__wrapper .add:active,
.books .container .books-list .books-list__item .bookPage-btn__wrapper .delete:active {
  transform: scale(0.9);
}
.books .container .books-list .books-list__item .bookPage-btn__wrapper .delete {
  background-color: rgba(255, 0, 0, 0.6);
}
.books .container .books-list .books-list__item .bookPage-btn__wrapper .add {
  background-color: rgba(0, 128, 0, 0.6);
}
.books .container .books-list .books-list__item .bookPage-btn__wrapper .count {
  display: block;
  width: 70px;
  height: 30px;
  font-size: 24px;
}
.books .container .books-list .books-list__item .bookPage-btn {
  width: 200px;
  height: 50px;
  border: 0.5px solid black;
  border-radius: 10px;
  font-size: 18px;
  color: black;
  background-color: white;
}
.books .container .books-list .books-list__item .bookPage-btn:hover {
  opacity: 0.6;
}
.books .container .books-list .books-list__item .bookPage-btn:active {
  transform: scale(0.8);
}
.books .container .books-list .books-list__item .heart-wrapper {
  position: absolute;
  top: 20px;
  left: 90%;
}
.books .container .books-list .books-list__item .heart-wrapper .heart {
  width: 15px;
  height: 15px;
  background-color: red;
  transform: rotate(45deg);
  position: relative;
}
.books .container .books-list .books-list__item .heart-wrapper .heart::after,
.books .container .books-list .books-list__item .heart-wrapper .heart::before {
  content: "";
  display: block;
  width: 15px;
  height: 15px;
  background-color: inherit;
  border-radius: 50%;
  position: absolute;
}
.books .container .books-list .books-list__item .heart-wrapper .heart::after {
  top: 0;
  left: -9px;
}
.books .container .books-list .books-list__item .heart-wrapper .heart::before {
  top: -9px;
  left: 0;
}
.books .container .books-list .books-list__item .heart-wrapper .whiteHeart {
  background-color: white;
}
.books .container .books-list .books-list__item .heart-wrapper .heart:hover {
  background-color: red;
}
.books .container .books-list .books-list__item .heart-wrapper:active {
  transform: scale(1.2);
}

@keyframes open {
  0% {
    width: 0px;
  }
  25% {
    width: 100px;
  }
  50% {
    width: 200px;
  }
  75% {
    width: 300px;
  }
  100% {
    width: 400px;
  }
}
@keyframes close {
  0% {
    padding: 5px;
    width: 400px;
    height: 30px;
    border: 0.5px solid black;
  }
  25% {
    padding: 5px;
    width: 300px;
    height: 30px;
    border: 0.5px solid black;
  }
  50% {
    padding: 5px;
    width: 200px;
    height: 30px;
    border: 0.5px solid black;
  }
  75% {
    padding: 5px;
    width: 100px;
    height: 30px;
    border: 0.5px solid black;
  }
  100% {
    width: 0px;
  }
}
