.main {
  margin-top: 5%;
  padding: 10px;
}
.main .container {
  margin-top: 10px;
  padding: 10px;
}
.main .container .description {
  display: flex;
  justify-content: center;
  align-items: center;
  -moz-column-gap: 10px;
       column-gap: 10px;
}
.main .container .description .description-text {
  display: flex;
  flex-direction: column;
  text-align: left;
  row-gap: 10px;
}
.main .container .description .description-text h1 {
  padding: 10px;
  width: 700px;
  font-size: 48px;
}
.main .container .description .description-text .description-btn {
  margin-left: auto;
  padding-top: 2px;
  text-align: center;
  width: 200px;
  height: 50px;
  font-size: 32px;
  border: 0.5px solid black;
  border-radius: 20px;
  color: black;
  background-color: rgba(243, 220, 194, 0.4);
}
.main .container .description .description-text .description-btn:hover {
  opacity: 0.8;
}
.main .container .description .description-text .description-btn:active {
  transform: scale(0.9);
}
.main .container .description .descriptionImg {
  margin: 20px;
  width: 500px;
  height: 350px;
  border-radius: 30px;
  box-shadow: 5px 5px 10px 10px;
  color: grey;
}

@media (max-width: 1200px) {
  .main {
    margin-top: 5%;
  }
  .main .container {
    max-width: 1000px;
  }
  .main .container .description .description-text h1 {
    width: 400px;
    font-size: 36px;
  }
  .main .container .description .descriptionImg {
    width: 400px;
    height: 250px;
  }
}
@media (max-width: 800px) {
  .main .container {
    margin-top: 5%;
    padding: 10px;
    max-width: 600px;
  }
  .main .container .description .description-text h1 {
    padding: 10px;
    width: 300px;
    font-size: 28px;
  }
  .main .container .description .description-text .description-btn {
    padding: 2px;
    width: 100px;
    height: 30px;
    font-size: 20px;
  }
  .main .container .description .descriptionImg {
    width: 350px;
    height: 200px;
  }
}
@media (max-width: 600px) {
  .main .container {
    margin-top: 10%;
    padding: 10px;
    max-width: 500px;
  }
  .main .container .description {
    -moz-column-gap: 20px;
         column-gap: 20px;
  }
  .main .container .description .description-text h1 {
    padding: 10px;
    width: 200px;
    font-size: 20px;
  }
  .main .container .description .description-text .description-btn {
    padding: 3px 2px;
    width: 80px;
    height: 30px;
    font-size: 16px;
  }
  .main .container .description .descriptionImg {
    width: 250px;
    height: 200px;
  }
}
@media (max-width: 430px) {
  .main .container {
    max-width: 300px;
  }
  .main .container .description {
    flex-direction: column;
    -moz-column-gap: 10px;
         column-gap: 10px;
  }
  .main .container .description .description-text {
    align-items: center;
  }
  .main .container .description .description-text h1 {
    padding: 10px;
    width: 300px;
    font-size: 22px;
  }
  .main .container .description .description-text .description-btn {
    margin-left: 0;
    padding: 3px 2px;
    width: 80px;
    height: 30px;
    font-size: 16px;
  }
  .main .container .description .descriptionImg {
    width: 280px;
    height: 200px;
  }
}/*# sourceMappingURL=Main.css.map */