$pure-black: black;
$sandy-color: #f3dcc2;

.footer {
    margin-top: auto;
    background-color: $sandy-color;
    border-top: 1px solid $pure-black;
    .footer-wrapper {
        padding: 10px;
        align-items: center;
        p {
            font-size: 24px;
            color: $pure-black;
        }
    }
}

@media (max-width: 1200px) {
    .footer {
        .container {
            padding: 0;
            max-width: 1000px;
        }
        .footer-wrapper {
            padding: 10px;
            align-items: center;
            p {
                font-size: 22px;
            }
        }
    }
}
@media (max-width:800px) {
    .footer {
        .container {
            padding: 0;
            max-width: 600px;
        .footer-wrapper {
            p {
                font-size: 18px;
            }
        }
        }
        
    }
}

@media (max-width:600px) {
    .footer {
        .container {
            padding: 0;
            max-width: 600px;
        .footer-wrapper {
            p {
                font-size: 18px;
            }
        }
        }
        
    }
}

@media (max-width:430px) {
    .footer {
        .container {
            padding: 0;
            max-width: 300px;
        .footer-wrapper {
            p {
                font-size: 14px;
            }
        }
        }
        
    }
}