$pure-black: black;
$pure-white: white;
$sandy-color: #f3dcc2;
.books {
    padding: 10px;
    
    .container {
        padding: 5px;

        .search {
            display: flex;
            justify-content: center;
            column-gap: 10px;
            margin-bottom: 40px;
            padding-left: 10px;
            align-content: center;
            .search-open {
                padding: 5px;
                width: 400px;
                height: 30px;
                border: .5px solid $pure-black;
                border-radius: 20px;
                animation: open 2s linear;
            }

            .search-closed {
                padding: 0px;
                width: 0px;
                height: 0px;
                border: 0px solid $pure-black;
                border-radius: 20px;
                animation: close 2s linear;
            }



            .search-img {
                width: 30px;
                height: 30px;
            } 

            .search-img:hover {
                opacity: .8;
            }
     
        }
        .books-list {
            display: flex;
            justify-content: flex-start;
            flex-wrap: wrap;
            position: relative;
            padding: 10px;
            gap: 20px;
            .books-list__item {
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                position: relative;
                row-gap: 5px;
                padding: 5px;
                width: 350px;
                height: 350px;
                border: .5px solid $sandy-color;
                border-radius: 20px;
                background-color: rgba($color: $sandy-color, $alpha: .2);
                transition: linear 2s;
                .books-list__img-wrapper {
                    width: 90%;
                    height: 90%;
                    border-radius: 20px;
                    .books-item__img {
                        margin-top: 5px;
                        width: 200px;
                        height: 250px;
                        border-radius: 15px;
                        box-shadow: 2px 2px 4px 4px;
                        color: grey;
                    }

                    .books-item__img:hover {
                        transform: scale(1.2);
                    }
                }
                    .books-item__title {
                        width: 70%;
                        color: $pure-black;
                        font-size: 14px;
                        border-bottom: 1px solid rgba($color: $pure-black, $alpha: .2);
                    }
                    .books-item__price {
                        align-items: start;
                        width: 70%;
                        color: $pure-black;
                        border-bottom: 1px solid rgba($color: $pure-black, $alpha: .2);
                    }
    
                    .ageRestrictions {
                        display: block;
                        position: absolute;
                        top: 90%;
                        left: 90%;
                        padding-top: 5px;
                        width: 30px;
                        height: 30px;
                        border-radius: 50%;
                        background-color: $pure-white;
                        border: 1px solid $pure-black;
                    }
    
                    .bookPage-btn__wrapper {
                        display: flex;
                        .delete,
                        .add {
                            width: 40px;
                            height: 30px;
                            border: .5px solid $pure-white;
                            border-radius: 40px;
                            font-size: 22px;
                            color: $pure-white;
                        }

                        .add:hover,
                        .delete:hover {
                            opacity: .8;
                        }

                        .add:active,
                        .delete:active {
                            transform: scale(.9);
                        }
    
                        .delete {
                            background-color: rgba($color: red, $alpha: .6);
                        }
    
                        .add {
                            background-color: rgba($color: green, $alpha: .6);
                        }
                        
    
                        .count {
                            display: block;
                            width: 70px;
                            height: 30px;
                            font-size: 24px;
                        }
    
                    }
    
                    .bookPage-btn {
                        width: 200px;
                        height: 50px;
                        border: .5px solid $pure-black;
                        border-radius: 10px;
                        font-size: 18px;
                        color: $pure-black;
                        background-color: $pure-white;
                    }
    
                    .bookPage-btn:hover {
                        opacity: .6;
                    }
    
                    .bookPage-btn:active {
                        transform: scale(.8);
                    }
    
                    .heart-wrapper {
                        position: absolute;
                        top: 20px;
                        left: 90%;
                        .heart {
                            width: 15px;
                            height: 15px;
                            background-color: red;
                            transform: rotate(45deg);
                            position: relative;
                        }
    
                        .heart::after,
                        .heart::before {
                            content: "";
                            display: block;
                            width: 15px;
                            height: 15px;
                            background-color: inherit;
                            border-radius: 50%;
                            position: absolute;
                        }
                        .heart::after {
                            top: 0;
                            left: -9px;
                        }
                        .heart::before {
                            top: -9px;
                            left: 0;
                        }
    
                        .whiteHeart {
                            background-color: $pure-white;
                        }

                        .heart:hover {
                            background-color: red;
                        }
                    }

                    .heart-wrapper:active {
                        transform: scale(1.2);
                    }
      
            }
        }  
    }   
}

@keyframes open {
    0% {
        width: 0px;
    }

    25% {
        width: 100px;
    }

    50% {
        width: 200px;
    }

    75% {
        width: 300px;
    }

    100% {
        width: 400px;
    }
}

@keyframes close {
    0% {
        padding: 5px;
        width: 400px;
        height: 30px;
        border: .5px solid $pure-black;
    }

    25% {
        padding: 5px;
        width: 300px;
        height: 30px;
        border: .5px solid $pure-black;
    }

    50% {
        padding: 5px;
        width: 200px;
        height: 30px;
        border: .5px solid $pure-black;
    }

    75% {
        padding: 5px;
        width: 100px;
        height: 30px;
        border: .5px solid $pure-black;
    }

    100% {
        width: 0px;
    }
}

@media (max-width:1100px) {
    .books {
        .container {
            .books-list {
                justify-content: center;
            }
        }
    }
}

@media (max-width:800px) {
    .books {
        
        .container {
    
            .books-list {
                justify-content: center;
                .books-list__item {
                    width: 200px;
                    height: 250px;
                    .books-list__img-wrapper {
                        
                        .books-item__img {
                            margin-top: 5px;
                            width: 120px;
                            height: 150px;
                        }
                    }
                        .books-item__title {
                            font-size: 12px;
                        }
                        .books-item__price {
                            font-size: 12px;
                        }
        
                        .ageRestrictions {
                            padding-top: 2px;
                            top: 90%;
                            left: 88%;
                            width: 20px;
                            height: 20px;
                            font-size: 10px;
                        }
        
                        .bookPage-btn__wrapper {
                            .delete,
                            .add {
                                width: 25px;
                                height: 20px;
                                font-size: 16px;
                            }
                            .count {
                                width: 40px;
                                height: 20px;
                                font-size: 18px;
                            }
        
                        }
        
                        .bookPage-btn {
                            width: 100px;
                            height: 20px;
                            font-size: 12px;
                        }

        
                        .heart-wrapper {
                            top: 8%;
                            left: 88%;
                        }
                }
            }  
        }   
    }
}

