$pure-black: black;
$pure-white: white;
$sandy-color: #f3dcc2;

.bookPage {
    .container {
        margin-top: 10px;
        background-color: rgba($color: grey, $alpha: .1);
        border-radius: 50px;
        border: .5px solid $pure-white;
        .bookPage-wrapper {
            display: flex;
            justify-content: center;
            align-items: center;
            column-gap: 30px;
            .bookPage-img {
                position: relative;
                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: center;
                row-gap: 20px;
                img {
                    width: 400px;
                    height: 600px;
                    border-radius: 40px;
                    box-shadow: 5px 5px 10px 10px rgba($color: $pure-black, $alpha: .2);
                }
                .bookPage-img__list {
                    position: absolute;
                    left: -27%;
                    display: flex;
                    flex-direction: column;
                    row-gap: 20px;
                    img {
                        width: 90px;
                        height: 120px;
                        border-radius: 20px;
                        box-shadow: none;
                    }

                    img:hover {
                        opacity: .8;
                    }

                    img:active {
                        transform: scale(.9);
                    }
                }

                .bookPage-img__title {
                    font-weight: 700;
                    font-size: 24px;
                    color: $pure-black;
                }
                
                .ageRestrictions {
                    position: absolute;
                    top: 93%;
                    left: 87%;
                    padding-top: 5px;
                    display: block;
                    width: 40px;
                    height: 40px;
                    background-color: rgba($color: $pure-white, $alpha: .8);
                    border-radius: 50%;
                    border: .5px solid $pure-black;
                    font-size: 24px;
                }

                .back-btn {
                    padding: 3px;
                    position: absolute;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    top: 8%;
                    left: -22%;
                    width: 60px;
                    height: 50px;
                    border-radius: 15px;
                    border: .5px solid $pure-white;
                    font-size: 32px;
                    background-color: rgba($color: $pure-black, $alpha: .2);
                    color: $pure-white;
                }

                .back-btn:hover {
                    opacity: .8;
                }

                .back-btn:active {
                    transform: scale(.9);
                }
               
            }

            .bookPage-description {
                display: flex;
                flex-direction: column;
                align-items: flex-start;
                row-gap: 10px;
                padding: 10px;
                width: 500px;
                height: 600px;
                .bookPage-description__title {
                    font-size: 30px;
                    color: $pure-black;
                }
                .bookPage-description__list {
                    width: 400px;
                    .bookPage-description__item {
                        display: flex;
                        align-items: center;
                        justify-content: space-between;
                        padding: 5px;
                        border-bottom: 1px solid $sandy-color;
                        .static-info {
                            font-size: 18px;
                        }

                        .changing-info {
                            font-size: 18px;
                            font-weight: 700;
                        }
                    }
                }
                .description {
                    display: flex;
                    flex-direction: column;
                    align-items: flex-start;
                    row-gap: 5px;
                    p {
                        text-align: start;
                    }
                }
            }

            .bookPage-btn__wrapper {
                display: flex;
                .delete,
                .add {
                    width: 60px;
                    height: 50px;
                    border: .5px solid $pure-white;
                    border-radius: 20px;
                    font-size: 36px;
                    color: $pure-white;
                }

                .add:hover,
                .delete:hover {
                    opacity: .8;
                }

                .add:active,
                .delete:active {
                    transform: scale(.9);
                }

                .delete {
                    background-color: rgba($color: red, $alpha: .8);
                }

                .add {
                    background-color: rgba($color: green, $alpha: .8);
                }

                .count {
                    display: block;
                    padding: 5px;
                    width: 90px;
                    height: 50px;
                    font-size: 36px;
                    color: $pure-black;
                }

            }
            .bookPage-add {
                position: relative;
                display: flex;
                justify-content:space-around;
                align-items: center;
                margin-top: auto;
                width: 450px;
                height: 60px;
                background-color: rgba($color: $sandy-color, $alpha: .6);
                border-radius: 20px;
                
                .bookPage-btn {
                    width: 60%;
                    height: 90%;
                    font-size: 24px;
                    border-radius: 20px;
                    background-color: $pure-white;
                    border:  .5px solid $pure-black;
                }

                .bookPage-btn:hover {
                    opacity: 0.6;
                }

                .bookPage-btn:active {
                    transform: scale(.9);
                }

                .heart-wrapper {
                    position: absolute;
                    top: 20px;
                    left: 90%;
                    .heart {
                        width: 20px;
                        height: 20px;
                        background-color: red;
                        transform: rotate(45deg);
                        position: relative;
                    }

                    .heart::after,
                    .heart::before {
                        content: "";
                        display: block;
                        width: 20px;
                        height: 20px;
                        background-color: inherit;
                        border-radius: 50%;
                        position: absolute;
                    }
                    .heart::after {
                        top: 0;
                        left: -9px;
                    }
                    .heart::before {
                        top: -9px;
                        left: 0;
                    }

                    .whiteHeart {
                        background-color: $pure-white;
                    }

                    .heart:hover {
                        background-color: red;
                    }
                }

                .heart-wrapper:active {
                    transform: scale(1.2);
                }
                   
            }
        }
    }
}

