*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

ul, ol {
  list-style: none;
}

a {
  text-decoration: none;
}

:root {
  scroll-behavior: smooth;
}

.BookStore {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    background-color: white;
}

/* General */

.container {
    margin: 0 auto;
    padding: 20px 0;
    text-align: center;
    max-width: 1200px;
}