$pure-black: black;
$pure-white: white;
$sandy-color: #f3dcc2;

.basket {
    padding: 10px;

    .container {
        display: flex;
        position: relative;
        .basket-books {
            display: flex;
            flex-direction: column;
            row-gap: 10px;
            padding: 10px 20px 10px;

            .basket-list__item {
                position: relative;
                padding: 10px;
                width: 500px;
                height: 220px;
                background-color: rgba($color: $sandy-color, $alpha: .4);
                border: .5px solid $pure-black;
                border-radius: 20px;

                .ageRestrictions {
                    display: block;
                    padding-top: 3px;
                    width: 20px;
                    height: 20px;
                    position: absolute;
                    top: 85%;
                    left: 27%;
                    border-radius: 50%;
                    background-color: rgba($color: $pure-white, $alpha: .8);
                    font-size: 12px;
                }
                

                .basket-info__wrapper {
                    display: flex;
                    flex-direction: row;
                    column-gap: 30px;
                    align-items: center;
                    .basket-list__img-wrapper {

                        .basket-item__img {
                            width: 150px;
                            height: 200px;
                            border-radius: 20px;
                            color: grey;
                            box-shadow: 2px 2px 4px 4px;
                        }
                    }

                    

                    .basket-info {
                        display: flex;
                        flex-direction: column;
                        row-gap: 20px;
                        width: 50%;
                        .books-item__price {
                            font-size: 22px;
                            text-align: left;
                        }

                        .basket-item__title {
                            font-size: 24px;
                            border-bottom: 0.5px solid $pure-black;
                        }

                        .bookPage-btn__wrapper {
                            display: flex;
                            flex-direction: row;
                            column-gap: 10px;
                            justify-content: left;
                            .delete,
                            .add {
                                display: block;
                                width: 30px;
                                height: 30px;
                                border-radius: 40px;
                                border: .5px solid $pure-white;
                                font-size: 24px;
                                color: $pure-white;
                            }

                            .delete {
                                background-color: rgba($color: red, $alpha: .8);
                            }

                            .add {
                                background-color: rgba($color: green, $alpha: .8);
                            }

                            .add:hover,
                            .delete:hover {
                                opacity: .8;
                            }

                            .add:active,
                            .delete:active {
                                transform: scale(.9);
                            }

                            .count {
                                font-size: 24px;
                            }
                        }

                        .books-item__total {
                            text-align: right;
                            font-size: 20px;
                        }
                    }
                }

                .heart-wrapper {
                    position: absolute;
                    top: 7%;
                    left: 35%;
                    .heart {
                        width: 15px;
                        height: 15px;
                        background-color: red;
                        transform: rotate(45deg);
                        position: relative;
                    }

                    .heart::after,
                    .heart::before {
                        content: "";
                        display: block;
                        width: 15px;
                        height: 15px;
                        background-color: inherit;
                        border-radius: 50%;
                        position: absolute;
                    }
                    .heart::after {
                        top: 0;
                        left: -9px;
                    }
                    .heart::before {
                        top: -9px;
                        left: 0;
                    }

                    .whiteHeart {
                        background-color: $pure-white;
                    }

                    .heart:hover {
                        background-color: red
                    }
                }

                .heart-wrapper:active {
                    transform: scale(1.2);
                }

                .books-item__delete {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    position: absolute;
                    top: 3%;
                    left: 92%;
                    width: 25px;
                    height: 25px;
                    border-radius: 50%;
                    border: .5px solid $pure-white;
                    background-color: rgba($color: red, $alpha: .6);
                    font-size: 22px;
                }

                .books-item__delete:hover {
                    background-color: rgba($color: red, $alpha: .8);
                }

                
            }


        }

        .basket-order {
            display: flex;
            flex-direction: column;
            row-gap: 10px;
            padding: 20px;
            width: 400px;
            height: 200px;
            position: fixed;
            top: 30%;
            left: 55%;
            border: .5px solid $pure-black;
            border-radius: 40px;
            text-align: left;
            background-color: rgba($color: $sandy-color, $alpha: .2);

            .basket-title {
                font-size: 24px;
            }

            .total-price {
                display: flex;
                justify-content: space-between;
                font-size: 22px;
                border-bottom: .5px solid $pure-black;

                span {
                   color: green
                }
            }

            .total-positions {
                display: flex;
                justify-content: space-between;
                font-size: 22px;
                border-bottom: .5px solid $pure-black;
            }

            .btn-order {
                margin-top: auto;
                height: 50px;
                border-radius: 20px;
                border: .5px solid $pure-black;
                font-size: 24px;
                background-color: $pure-white;
            }

            .btn-order:hover {
                opacity: .6;
            }

            .btn-order:active {
                transform: scale(.9);
            }

        }
    }
}

@media (max-width:1000px) {

    .basket {
        padding: 10px;
    
        .container {
            display: flex;
            flex-direction: column;
            align-items: center;
            .basket-books {
                align-items: center;    
            }
    
            .basket-order {
                position: unset;  
                }  
            }
    }
}

@media (max-width:500px) {
    .basket {
        padding: 10px;
    
        .container {
            row-gap: 40px;
            .basket-books {
                align-items: center;
    
                .basket-list__item {
                    padding: 5px;
                    width: 300px;
                    height: 120px;
    
                    .ageRestrictions {
                        top: 77%;
                        left: 27%;
;                        width: 15px;
                        height: 15px;
                        font-size: 8px;
                    }
                    
    
                    .basket-info__wrapper {
                        justify-content: space-evenly;
                        align-items: center;
                        .basket-list__img-wrapper {
    
                            .basket-item__img {
                                width: 80px;
                                height: 100px;
                            }
                        }
    
                        
    
                        .basket-info {
                            row-gap: 7px;
                            .books-item__price {
                                font-size: 12px;
                            }
    
                            .basket-item__title {
                                font-size: 16px;
                            }
    
                            .bookPage-btn__wrapper {
                                column-gap: 4px;
                                .delete,
                                .add {
                                    width: 20px;
                                    height: 20px;
                                    border-radius: 10px;
                                    font-size: 14px;
                                }

    
    
    
                                .count {
                                    font-size: 18px;
                                }
                            }
    
                            .books-item__total {
                                text-align: right;
                                font-size: 14px;
                            }
                        }
                    }
    
                    .heart-wrapper {
                        .heart {
                            width: 10px;
                            height: 10px;
                        }
    
                        .heart::after,
                        .heart::before {
                            width: 10px;
                            height: 10px;
                        }
                        .heart::after {
                            left: -7px;
                        }
                        .heart::before {
                            top: -7px;
                        }
                    }
    
                    .books-item__delete {
                        width: 20px;
                        height: 20px;
                        font-size: 16px;
                    }
                    
                }
    
    
            }
    
            .basket-order {
                width: 300px;

                }
        }
    }
}