.footer {
  margin-top: auto;
  background-color: #f3dcc2;
  border-top: 1px solid black;
}
.footer .footer-wrapper {
  padding: 10px;
  align-items: center;
}
.footer .footer-wrapper p {
  font-size: 24px;
  color: black;
}

@media (max-width: 1200px) {
  .footer .container {
    padding: 0;
    max-width: 1000px;
  }
  .footer .footer-wrapper {
    padding: 10px;
    align-items: center;
  }
  .footer .footer-wrapper p {
    font-size: 22px;
  }
}
@media (max-width: 800px) {
  .footer .container {
    padding: 0;
    max-width: 600px;
  }
  .footer .container .footer-wrapper p {
    font-size: 18px;
  }
}
