.nav {
  padding: 10px;
  background-color: #f3dcc2;
  border-bottom: 1px solid black;
  transition: 0.5 ease-in;
}
.nav .nav-wrapper {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  margin-right: 10%;
  padding: 10px;
}
.nav .nav-wrapper .logo {
  margin-right: auto;
  font-size: 64px;
  font-weight: 700;
}
.nav .nav-wrapper .logo a {
  color: black;
}
.nav .nav-wrapper .nav-list {
  display: flex;
  flex-wrap: wrap;
  -moz-column-gap: 30px;
       column-gap: 30px;
  padding: 5px;
}
.nav .nav-wrapper .nav-list .nav-list__item {
  position: relative;
  font-size: 24px;
}
.nav .nav-wrapper .nav-list .nav-list__item a {
  color: black;
}
.nav .nav-wrapper .nav-list .nav-list__item:hover {
  opacity: 0.6;
  -webkit-text-decoration: underline white;
          text-decoration: underline white;
}
.nav .nav-wrapper .nav-list .basketCount,
.nav .nav-wrapper .nav-list .favoritesCount {
  position: absolute;
  top: 60%;
  left: 100%;
  width: 25px;
  height: 25px;
  border-radius: 50%;
  background-color: rgba(255, 255, 255, 0.4);
  font-size: 22px;
}

@media (max-width: 1200px) {
  .nav {
    padding: 5px;
  }
  .nav .container {
    padding: 0;
    max-width: 1000px;
  }
  .nav .nav-wrapper .logo {
    font-size: 54px;
  }
}
@media (max-width: 800px) {
  .nav .container {
    max-width: 600px;
    padding: 0;
  }
  .nav .container .nav-wrapper .logo {
    font-size: 32px;
  }
  .nav .container .nav-wrapper .nav-list {
    -moz-column-gap: 20px;
         column-gap: 20px;
  }
  .nav .container .nav-wrapper .nav-list .nav-list__item {
    font-size: 18px;
  }
  .nav .container .nav-wrapper .nav-list .basketCount,
  .nav .container .nav-wrapper .nav-list .favoritesCount {
    font-size: 18px;
  }
}
@media (max-width: 600px) {
  .nav .container {
    max-width: 500px;
    padding: 0;
  }
  .nav .container .nav-wrapper .logo {
    font-size: 28px;
  }
  .nav .container .nav-wrapper .nav-list .nav-list__item {
    font-size: 18px;
  }
  .nav .container .nav-wrapper .nav-list .basketCount,
  .nav .container .nav-wrapper .nav-list .favoritesCount {
    position: absolute;
    top: 60%;
    left: 100%;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background-color: rgba(255, 255, 255, 0.4);
    font-size: 18px;
  }
}
@media (max-width: 430px) {
  .nav .container {
    max-width: 300px;
    padding: 0;
  }
  .nav .container .nav-wrapper .logo {
    font-size: 24px;
  }
  .nav .container .nav-wrapper .nav-list {
    flex-direction: column;
    align-items: start;
    row-gap: 3px;
  }
  .nav .container .nav-wrapper .nav-list .nav-list__item {
    font-size: 16px;
  }
  .nav .container .nav-wrapper .nav-list .basketCount,
  .nav .container .nav-wrapper .nav-list .favoritesCount {
    padding-top: 1px;
    width: 15px;
    height: 15px;
    top: 0%;
    left: 120%;
    font-size: 12px;
  }
}/*# sourceMappingURL=Navbar.css.map */